*{margin:0;padding:0;box-sizing:border-box}.App{transition:all 1s ease}.activeList{margin-left:10%}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:0 1rem}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:50%}.play-control svg{cursor:pointer}.track{background:lightblue;height:1rem;width:100%;position:relative;overflow:hidden;border-radius:1rem}.aniamte-track{background:#6e6969;height:100%;width:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(100%);transform:translateX(100%);pointer-events:none;border-radius:1rem}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:60%}}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem;font-family:'Anton', sans-serif;color:#6fb466}.song-container h3{font-size:1rem;font-family:'Shadows Into Light', cursive}@media screen and (max-width: 768px){.song-container img{width:60%}}.song-list{position:fixed;top:0;left:0;width:25vw;height:100vh;background-color:#c1b5eb;box-shadow:5px 5px 50px #ccc;overflow-y:scroll;transition:all 0.5s linear}.song-list h2{padding:1vw;text-align:center;font-family:'Luckiest Guy', cursive}.song-list-items{display:flex;flex-direction:column;padding:4vh 2vw;align-items:center}.song-list-items img{width:40%}.song-item{margin-bottom:4vh;display:flex;flex-direction:row;cursor:pointer}.song-item:hover{background:#c8c8c8}.song-discription{padding-left:1vw;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-discription h3{font-size:1rem;margin-bottom:1vh;font-family:'Lato', sans-serif}.song-discription h4{font-size:0.7rem;font-family:'Lato', sans-serif}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.selected{background-color:#6f7d8e}.displayList{-webkit-transform:translateX(-30vw);transform:translateX(-30vw)}@media screen and (max-width: 768px){.song-list{width:100%;transition:all 1s ease}.displayList{-webkit-transform:translateX(-100vw);transform:translateX(-100vw)}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;cursor:pointer;padding:0.5rem;-webkit-transform:all 0.3s ease;transform:all 0.3s ease}nav button:hover{background:#414141;color:white;border:2px solid #f0f0f0}#button-toggle{margin-right:5px}#app-title{font-family:'Lato', sans-serif;font-weight:bold;color:red}@media screen and (max-width: 768px){nav button{z-index:10;position:fixed;top:0.7vh;right:5vw}#app-title{z-index:10;position:fixed;top:0.7vh;left:5vw}}

